.analystSectionStyle {
  display: inline-block;
  padding-left:5%;
  padding-right:5%;
}

.analystTableHeader {
  margin-top: 50px;
  color: #00994c;
  margin-bottom: 20px;
  display: inline-block;
}

.analystUserActions {
  margin-right: 10px;
}

.analystTableStyles thead {
  text-align: center;
  overflow: auto;
  white-space: nowrap;
}
.analystTableStyles td {
  max-width: 275px;
  white-space: nowrap;
  overflow: auto;
  text-overflow: inherit;
  text-align: center;
  vertical-align: middle;
}
